import { Component, OnInit } from '@angular/core';
import { Person } from '../objets/Person';
import { SteamService } from '../steam.service';
import { PersonService } from '../person.service';

@Component({
  selector: 'app-params',
  templateUrl: './params.component.pug',
  styleUrls: ['./params.component.css']
})
export class ParamsComponent implements OnInit {
  lesPayas: any;

  constructor(private steamService: SteamService, private personService: PersonService) { }

  ngOnInit(): void {
    this.lesPayas = [];
    this.personService.getAllPaya().subscribe(payas => {
      payas.forEach(paya => {
        let person: Person = { steamid: paya.steamid, username: paya.nomPersonne };
        if (!this.lesPayas.find(unPaya => unPaya.steamid == person.steamid)) { this.lesPayas.push(person); }
      });
    });
  }

  public updateGames() {
    console.log(this.lesPayas);
    this.lesPayas.forEach(paya => {
      console.log(paya.username);
      this.steamService.updateGamesPlayer(paya.steamid).subscribe(result => console.log('mise à jour faite pour ' + paya.username));
    });
  }


}
