import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from "@angular/forms";

import { SteamService } from "../steam.service";
import { Game } from '../objets/Game';
import { Person } from '../objets/Person';
import { PersonService } from '../person.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.pug',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  games: Game[];
  AllGames: any;
  lesPayas: Person[];
  payas: Promise<Person[]>;
  selectedPerson1: string;
  selectedPerson2: string;

  isSubmitted: boolean;
  userSelection = this.fb.group({
    firstUser: ['1', [Validators.required]],
    secondUser: ['1', [Validators.required]],
    thirdUser: ['1', [Validators.required]],
    forthUser: ['1', [Validators.required]]
  })
  messageErreur: string;
  erreurs: boolean;


  constructor(private steamService: SteamService, private personService: PersonService, public fb: FormBuilder) { }

  ngOnInit(): void {
    this.lesPayas = [];
    this.games = new Array;

    var call = this.personService.getAllPaya();
    this.payas = call.toPromise();
    call.subscribe(payas => {
      payas.forEach(paya => {
        let person: Person = { steamid: paya.steamid, username: paya.nomPersonne };
        if (this.lesPayas.find(unPaya => unPaya.steamid != person.steamid)) { this.lesPayas.push(person); }
      });
    });
  }

  private compareGames() {
    let steamid1 = this.userSelection.controls.firstUser.value;
    let steamid2 = this.userSelection.controls.secondUser.value;
    let steamid3 = this.userSelection.controls.thirdUser.value;
    let steamid4 = this.userSelection.controls.forthUser.value;
    this.getGames(steamid1, steamid2, steamid3, steamid4);
  }


  private getGames(steamid1: string, steamid2: string, steamid3: string, steamid4: string) {
    this.steamService.getGames(steamid1)
      .subscribe(result1 => {
        var games1 = result1;

        this.steamService.getGames(steamid2)
          .subscribe(result2 => {
            var games2 = result2;
            // console.log(typeof(games1));
            // console.log(typeof(games2));

            if (typeof (games1) != 'undefined' && typeof (games2) != 'undefined') {
              this.games = this.getIntersect(games1, games2);
              if (steamid3 != '1') {
                this.steamService.getGames(steamid3)
                  .subscribe(result3 => {
                    var games3 = result3;
                    this.games = this.getIntersect2(games1, games2, games3);
                    if (steamid4 != '1') {
                      this.steamService.getGames(steamid4)
                        .subscribe(result4 => {
                          var games4 = result4;
                          this.games = this.getIntersect3(games1, games2, games3, games4);
                          this.getGamesInfos();
                        });
                    } else {
                      this.getGamesInfos();
                    }
                  });
              } else {
                this.getGamesInfos();
              }
            }
          });
      });
  }

  private getGamesInfos() {
    this.games.forEach(game => {
      this.steamService.getGameInfo(game.appid).subscribe(gameInfo => {
        game.appName = gameInfo[0].name;
      });
    });
  }

  private getIntersect(arr1, arr2) {
    var temp = [];
    for (var i = 0; i < arr1.length; i++) {
      for (var k = 0; k < arr2.length; k++) {
        if (arr1[i].appid == arr2[k].appid) {
          arr1[i].PlayedTime = (arr1[i].PlayedTime + arr2[k].PlayedTime) / 2 / 60
          temp.push(arr1[i]);
          break;
        }
      }
    }
    return temp;
  }

  private getIntersect2(arr1, arr2, arr3) {
    var temp = [];
    for (var i = 0; i < arr1.length; i++) {
      for (var k = 0; k < arr2.length; k++) {
        for (var l = 0; l < arr3.length; l++) {
          if (arr1[i].appid == arr2[k].appid && arr2[k].appid == arr3[l].appid) {
            arr1[i].PlayedTime = (arr1[i].PlayedTime + arr2[k].PlayedTime + arr3[l].PlayedTime) / 2 / 60
            temp.push(arr1[i]);
            break;
          }
        }
      }
    }
    return temp;
  }

  private getIntersect3(arr1, arr2, arr3, arr4) {
    var temp = [];
    for (var i = 0; i < arr1.length; i++) {
      for (var k = 0; k < arr2.length; k++) {
        for (var l = 0; l < arr3.length; l++) {
          for (var m = 0; m < arr4.length; m++) {
            if (arr1[i].appid == arr2[k].appid && arr2[k].appid == arr3[l].appid && arr3[l].appid == arr4[m].appid) {
              arr1[i].PlayedTime = (arr1[i].PlayedTime + arr2[k].PlayedTime + arr3[l].PlayedTime + arr4[m].PlayedTime) / 2 / 60
              temp.push(arr1[i]);
              break;
            }
          }
        }
      }
    }
    return temp;
  }

  public onSubmit() {
    this.isSubmitted = true;
    this.erreurs = false;
    if (!this.userSelection.valid) {
      this.messageErreur = "Veuillez selectionner au moins un joueur sur les deux listes";
      this.erreurs = true;
    } else if (this.isMemeJoueur()) {
      this.erreurs = true;
      this.messageErreur = "Veuillez selectionner deux joueurs différents";
    } else {
      this.compareGames();
      // alert(JSON.stringify(this.userSelection.value));
    }
    return false;
  }

  private isMemeJoueur(): boolean {
    let steamid1 = this.userSelection.controls.firstUser.value;
    let steamid2 = this.userSelection.controls.secondUser.value;
    return steamid1 === steamid2;
  }
}
